/**
 * –ü—ñ–¥—Å–≤—ñ—á—É—î —á–∏—Å–ª–æ–≤—ñ –ø–æ–∫–∞–∑–Ω–∏–∫–∏ —É —Ç–µ–∫—Å—Ç—ñ: 30%, +65, 2s, 5 seconds, —Ç–æ—â–æ.
 * –û–±–≥–æ—Ä—Ç–∞—î —ó—Ö —É <span class="stat-highlight"> –¥–ª—è —Å—Ç–∏–ª—ñ–∑–∞—Ü—ñ—ó.
 */
export const highlightValues = (text: string): string => {
  return text.replace(
    /(\+?\d+(?:\.\d+)?\s*%|\+\d+(?:\.\d+)?|\d+(?:\.\d+)?\s*(?:seconds?|—Å–µ–∫—É–Ω–¥[–∏–∞]?|—Å–µ–∫\.?|HP|hp|MP|mp|s\b))/g,
    '<span class="stat-highlight">$1</span>'
  );
};
